import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders, HttpParams} from '@angular/common/http';
import { environment as Constants } from './../../environments/environment';
import { Router} from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  private url = '';
  constructor(private http: HttpClient,
    private router: Router,) { 
    this.url = Constants.API_URL
  }

    isLoggedIn(failed_redirect = false,success_redirect = false){
        if(!localStorage.getItem('auth') || localStorage.getItem('auth') == '' || localStorage.getItem('auth') == undefined){
          if(failed_redirect){
            this.router.navigate(["/authentication/signin"]);
          }
            return false;
        }else{
            if(success_redirect){
              this.router.navigate(["/dashboard"]);
            }
            return true;
        } 
    }

    userIsNotLogin(){
      this.isLoggedIn(false,true);
    }

    login(data){
      const params = new HttpParams({
        fromObject: {
          grant_type: 'password',
          username: data.email,
          password: data.password,
        }
      });

        const httpOptions = {
          headers: new HttpHeaders({
            'Content-Type': 'application/x-www-form-urlencoded',
          })
        };
        data.grant_type = 'password';
        var result :any;
        result =  this.http.post(Constants.logIn,params,httpOptions);
        return result;
    }
   
    user_register(data){
      data.Firstname = data.fname;
      data.Lastname = data.lname;
      data.Email = data.email;
      data.password = data.pwd;
      data.Street = data.lname;
      data.Plc = data.lname;
      data.City = data.lname;
      data.Country = data.lname;
      data.TaxId = data.lname;
      let reqHeaders = new HttpHeaders().set('Content-Type', 'application/json');
      return this.http.post<any>(Constants.UserRegister,data,{ headers: reqHeaders }).map(res => res);
  }
    resetPassword(data){
            return this.http.post<any>(Constants.CompleteResetPassword,data).map(res => res);
    }

    forgetPasword(data){
        return this.http.post<any>(Constants.ResetPassword,data).map(res => res);
    }
}
